import React, { useContext } from 'react';
import PropTypes from 'prop-types';
//
import { context_api } from '../../../../../_context/ContextAPI';
import { context_post } from '../../../../../_context/post/ContextPost';
//
import { openScreenNotice } from '../../../../_screen_once/notice/ScreenNotice';
//
import LikeShareCmtElm from '../elm/LikeShareCmtElm';
//
import './LikeShareCmt.scss';

//
LikeShareCmt.propTypes = {
    user_reacted_ix: PropTypes.number,
    enabled_like: PropTypes.bool,
    //
    enabled_cmt: PropTypes.bool,
    count_comment: PropTypes.number,
    //
    enabled_share: PropTypes.bool,
    count_share: PropTypes.number,
};

LikeShareCmt.defaultProps = {
    enabled_like: true,
    enabled_share: true,
    enabled_cmt: true,
};

//
function LikeShareCmt({
    parent_id,
    //
    user_reacted_ix,
    enabled_like,
    //
    enabled_cmt,
    count_comment,
    //
    enabled_share,
    count_share,
    count_user_shared,
    //
    handleClickBtnCmt,
}) {
    //
    const { openScreenOnce, closeScreenOnce } = useContext(context_api);
    const { ws_send, ws_type_post } = useContext(context_post);

    //
    function changeTypeLike(new_type_like) {
        ws_send({
            id: parent_id,
            type: ws_type_post + '_like',
            type_like: new_type_like,
        });
    }
    //
    function sharePost() {
        if (count_user_shared < 10) {
            ws_send({
                id: parent_id,
                type: ws_type_post + '_share',
            });
        } else {
            openScreenNotice({
                openScreenOnce: openScreenOnce,
                ComponentNotice: (
                    <div className="display-flex-center w-250px padding-20px brs-10px bg-shadow-5">
                        <div className="padding-y-20px text-align-center font-600 text-white font-17px">
                            You can not share more than 10 times
                        </div>
                    </div>
                ),
            });

            setTimeout(() => {
                closeScreenOnce()
            }, 1500);
        }
    }

    //
    return (
        <div className="LikeShareCmt">
            <LikeShareCmtElm
                user_reacted_ix={user_reacted_ix}
                enabled_like={enabled_like}
                enabled_cmt={enabled_cmt}
                count_comment={count_comment}
                enabled_share={enabled_share}
                count_share={count_share}
                //
                changeTypeLike={changeTypeLike}
                handleClickBtnCmt={handleClickBtnCmt}
                sharePost={sharePost}
            />
        </div>
    );
}

export default LikeShareCmt;
